/*
 * Custom translucent site header
 */
.site-info{
  position: fixed;
  height: auto;
  z-index: 100;
}
.site-header {
  padding: 0px;
  background: linear-gradient(black,0,#096883);
  box-shadow: 3px 10px 8px 0 rgba(0,0,0,0.16), 20px 10px 20px 0 rgba(0,0,0,0.12);
}
.site-header a {
  color: white;
  transition: ease-in-out color .15s;
  font-size: 1.2em;
  font-weight: 300;
}
.site-header a:hover {
  color: #000;
  text-decoration: none;
}

/*
 * Dummy devices (replace them with your own or something else entirely!)
 */

.product-device {
  position: absolute;
  right: 10%;
  bottom: -30%;
  width: 300px;
  height: 540px;
  background-color: #f5f5f5;
  border-radius: 21px;
  transform: rotate(30deg);
}

.product-device::before {
  position: absolute;
  top: 10%;
  right: 10px;
  bottom: 10%;
  left: 10px;
  content: "";
  background-color: rgba(255, 255, 255, .1);
  border-radius: 5px;
}

.product-device-2 {
  top: -25%;
  right: auto;
  bottom: 0;
  left: 5%;
  background-color: #e5e5e5;
}

