.basic-scroll{
  z-index: 3;
  position: fixed;
  width: 2em;
  height: 2em;
  top: 1%;
  left:97%;
  border-radius: 40%;
  background: linear-gradient(135deg, #3cdddd, #ff1ac6);
  -webkit-animation: turnInfi 2s linear infinite;
          animation: turnInfi 2s linear infinite;
}
@-webkit-keyframes turnInfi{
  from{
    transform: rotate3d(0,0,0,0);
  }
  from{
    transform: rotate3d(0.7, 0.3, 1, 360deg);
  }
}
@keyframes turnInfi{
  from{
    transform: rotate3d(0,0,0,0);
  }
  from{
    transform: rotate3d(0.7, 0.3, 1, 360deg);
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: var(--constraint-height);
  z-index: -10;
  background-color: var(--contrast-color);
}

.containers {
  display: flex;
  top: 0px;
  height: var(--constraint-height);
  justify-content: space-around;
  align-items: center;
  color: #fff;
  -webkit-animation: expand .8s ease forwards;
          animation: expand .8s ease forwards;
  background-color: var(--secondary-color);
  position: absolute;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  transition: all .8s ease;
}

