@keyframes rotatequick{
  0%{
    width: 10%;
    opacity: 0;
  }
  25%{
    width: 25%;
    transform: rotateZ(180deg);
  }
  50%{
    width: 50%;
    transform: rotateZ(360deg);
  }
  75%{
    width: 75%;
    transform: rotateZ(540deg);
  }
  100%{
    width: 100%;
    transform: rotateZ(720deg);
    opacity: 1;
  }
}