@keyframes showing{
  from{
    width: 0;
    height: 0;
    padding: 0;
  }
  to{
    width: 370px;
    height: 50px;
    padding: 0.75rem 1.5rem;
  }
}

.box {
  width: 5em;
  height: 5em;
  transform: translateX(var(--tx)) ;
  opacity: calc(2*var(--op));
  transition: transform .1s linear;
  will-change: transform;
}


