.heading-navigate{
    width: 100vw;
    z-index: 4;
    position: fixed;
}
.scroll-sticky{
    transition: all 0.5s linear;
    transform: rotateX(90deg);
}
.navigate-mb-btn{
    right: 5px;
    position: absolute;
    z-index: 5;
}
.navigate-mb-btn button{
    display: none;
}
.navigate-show-heading{
    position: fixed;
    top: 5px;
    right: 10px;
    z-index: 1021;
}
.ic-big{
    font-size: 1.3em;
}

@media (max-width:768px){
    .scroll-sticky{
        transition: all 0.5s linear;
        transform: rotateX(0    deg);
    }
    .navigate-mb-btn button{
        display: inline-block;
    }
    .hid-link{
        display: none;
    }
    .mobile-show .hid-link{
        display: inline-block;
    }
    .navigate-show-heading{
        display: none;
    }
}
