.b-w-page{
    min-height: 100vh;
    
}
.pg-tb-each{
    display: inline-block;
}

.b-w-header{
    color: black;
    padding: 15px 10px;
    box-shadow: 0px 2px 2px 0 rgba(0,0,0,0.10), 0px 4px 4px 0 rgba(0,0,0,0.05);
}

.item-focus{
    background-color: rgb(9, 30, 66);
    color: white;
}
.pale.item-focus{
    background-color: rgb(108, 121, 143);
}
.group-splice{    
    background: rgb(198, 198, 201);
    width: 1px;
    height: 30px;
    display: inline-block;
    margin: 0px 8px;
}
.pos-ab{
    position: relative;
}

.head-rp-side{
    padding: 5px 5px;
}
.head-replace-only{
    width: 150px;
    text-align: center;
    cursor: pointer;
    border-radius: 3px;
}
.head-replace-only:hover{
    background: rgb(230, 230, 230);
}
.head-replace-only div{
    border-radius: 3px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.each-icon{
    background: none;
    border: none;
    min-width: 30px;
    height: 30px;
    display: inline-block;
    text-align: center;
    cursor: pointer;
    border-radius: 3px;
    padding: 0px;
    margin: 3px;
}
.each-icon-bg{
    padding: 3px 1px;
    border-radius: 3px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.each-icon-bg-head{
    padding: 3px 1px;
    border-radius: 3px;
    width: 100%;
    height: 100%;
}
.each-icon:hover{
    background: rgb(228, 228, 230);
}
.lst-hidden{
    right: 15px;
    top: 35px;
    position: relative;
}

.lst-tool{
    position: absolute;
    background: white;
    border-radius: 3px;
    border: solid 1px rgb(226, 226, 226);
    min-width: 200px;
    font-size: 0.9em;
    box-shadow: 0px 3px 3px 0 rgba(0,0,0,0.08), 0px 6px 6px 0 rgba(0,0,0,0.06);

}
/* .each-lst-show{

} */
.each-cls-lst{
    /* font-style: italic; */
    font-size: 1em;
    display: inline-block;
    height: 40px;
    padding: 4px 0px;
    cursor: pointer;
    width: 100%;
    background: none;
    border: none;

}
.each-cls-lst:hover{
    background: rgb(248, 248, 248);
}   
.each-cls-lst .quick-key{
    float: right;
    /* margin-left: auto; */
    border-radius: 5px;
    font-size: 0.8em;
    color: gray;
    background-color: rgba(223, 225, 229, 0.5);
    padding: 3px 5px;
}
.list-heading-hiden{
    position: relative;
}

[contenteditable] {
    outline: 0px solid transparent;
  }

.lst-hidden-rpl{
    background: white;
    border-radius: 3px;
    border: solid 1px rgb(226, 226, 226);
    min-width: 250px;
    font-size: 0.9em;
    box-shadow: 0px 3px 3px 0 rgba(0,0,0,0.08), 0px 6px 6px 0 rgba(0,0,0,0.06);

    position: absolute;
    z-index: 500;    
    top: 35px;
    left: -10px;
}
.txt-color-contain{
    cursor: pointer;
    height: 30px;
    border-radius: 3px;
}
.txt-color-contain:hover{
    background: rgb(240, 240, 240);
}
.txt-color-bg{
    padding:  2px 5px;
    width: 100%;
    height: 100%;
    border-radius: 3px;
}
.txt-color-tb{
 display: flex;
 flex-direction: column;
 /* justify-content: center; */
}
.txt-color-tb .head{
    text-align: center;
    font-weight: 700;
}
.txt-color-tb .bor{
    margin-top: -3px;
    height: 10px;
    width: 15px;
    background:
     linear-gradient(
       to left,
       red 0, 
       red 33.3%,
       orange 33.3%, 
       orange 66.6%, 
       blue 66.6%, 
       blue )no-repeat;
   background-size:100% 3px ;
}
.each-color-show{
    padding: 10px;
}
.each-color-show span{
    width: 30px;
    height: 30px;
    display: inline-block;
    border-radius: 4px;
    margin-right: 3px;
    margin-left: 3px;
    padding: 2px;
    cursor: pointer;
    border: solid 1px rgb(230, 230, 230);
}

.each-color-show span:hover{
    border: solid 1px rgb(209, 209, 209);
}
.lst-hidden-color{
    background: white;
    border-radius: 3px;
    border: solid 1px rgb(226, 226, 226);
    min-width: 275px;
    box-shadow: 0px 3px 3px 0 rgba(0,0,0,0.08), 0px 6px 6px 0 rgba(0,0,0,0.06);
    position: absolute;
    z-index: 500;    
    top: 35px;
    left: -10px;
}
